import { useEffect, useRef } from 'react';
import { Method } from '@/lib/parse-api';
import { request } from 'topwrite';

let ID = 0;
const proxyElement = document.getElementById('kancloud-proxy');

export default function useRequest() {

    const proxyId = useRef<number>();

    const callback = useRef<{
        resolve: (value: any) => void,
        reject: (error?: any) => void
    }>();

    useEffect(() => {

        if (proxyElement) {
            const id = ++ID;

            const handleFetchEnd = function() {

                if (parseInt(proxyElement.getAttribute('proxy-id')!) === id) {

                    const payload = JSON.parse(proxyElement.getAttribute('data')!);

                    proxyElement.removeAttribute('data');
                    proxyElement.removeAttribute('proxy-id');

                    if (callback.current) {
                        callback.current.resolve(payload);
                    }
                }
            };

            proxyElement.addEventListener('fetchEnd', handleFetchEnd);

            proxyId.current = id;

            return () => {
                proxyElement.removeEventListener('fetchEnd', handleFetchEnd);
            };

        }
    }, []);

    return async (config: {
        url: string,
        params: { [k: string]: string },
        headers: { [k: string]: string },
        method: Method,
        contentType: 'application/x-www-form-urlencoded' | 'application/json' | 'multipart/form-data'
    }) => {

        if (proxyElement) {
            proxyElement.setAttribute('proxy-id', String(proxyId.current));
            proxyElement.setAttribute('data', JSON.stringify(config));

            const event = new CustomEvent('fetchStart', {
                bubbles: true,
                cancelable: true
            });

            proxyElement.dispatchEvent(event);

            return new Promise<any>((resolve, reject) => {
                callback.current = {
                    resolve,
                    reject
                };
            });
        }

        const { url, method, params, headers, contentType } = config;

        const requestHeaders = {
            'accept': '*/*',
            ...headers,
            'content-type': contentType
        };

        try {

            let data;
            switch (contentType) {
                case 'application/x-www-form-urlencoded':
                    data = new URLSearchParams(params);
                    break;
                case 'multipart/form-data':
                    data = new FormData;
                    for (let index in params) {
                        data.append(index, params[index]);
                    }
                    break;
                default:
                    data = params;
            }

            const { status, statusText, headers: responseHeaders, request: { responseText } } = await request({
                url,
                method,
                data,
                headers: requestHeaders
            });

            return {
                method,
                params,
                url,
                status,
                statusText,
                ok: statusText === 'OK',
                body: responseText,
                requestHeaders,
                responseHeaders
            };
        } catch (error: any) {
            let body = <span>
                {error.message}<br /><br />
                请求失败，具体错误信息请查看控制台<br /><br />
                如果错误信息中包含有类似以下信息的<br /><br />
                No 'Access-Control-Allow-Origin' header is present on the requested resource.<br /><br />
                This request has been blocked; the content must be served over HTTPS.<br /><br />
                则该api不支持跨域或者为非HTTPS请求，请安装我们的chrome浏览器扩展代理请求，
                <a href='https://chrome.google.com/webstore/detail/看云api插件代理/ocdkhocoglmchdnemmgnpkdnkdbpbkin'
                   target='_blank'>立即安装</a>，安装完成后请刷新当前页后调试
            </span>;

            return {
                method,
                params,
                url,
                status: 500,
                statusText: 'Server Error',
                ok: false,
                body: body,
                requestHeaders: requestHeaders
            };
        }

    };

}
