import { styled } from 'topwrite';
import useApi from '@/lib/use-api';
import Field from '@/components/api/field';


export default function Params() {

    const { info: { params, explore } } = useApi();

    if (params.length === 0) {
        return null;
    }

    return <Container>
        {params.map((param, index) => {
            return <Param key={index}>
                <div>
                    <Name>{param.name}</Name>
                    <Type>{param.type}</Type>
                </div>
                <div>
                    {param.required ? <Required>required</Required> : null}
                    <Desc>{param.desc}</Desc>
                </div>
                <div>
                    {explore ? <Field param={param} /> : param.default}
                </div>
            </Param>;
        })}
    </Container>;
}
const Name = styled.div`
  font-weight: bold;
`;

const Desc = styled.div`
  color: #767676;
  font-size: .85em;
`;
const Required = styled.div`
  color: #db2828;
  font-size: .85em;
`;

const Type = styled.div`
  color: #a9a9a9;
  font-size: .85em;
`;

const Param = styled.div`
  display: flex;
  border: 1px solid rgba(0, 0, 0, .1);
  border-top: none;
  line-height: 1.8;

  & > div {
    padding: 5px 12px;
    flex: 1;
    justify-content: center;
    display: flex;
    flex-direction: column;

    &:last-child {
      text-align: right;
    }
  }

  &:hover {
    background-color: rgb(241, 241, 241);
  }
`;

const Container = styled.div`

`;
