import { createContext, PropsWithChildren, useContext, useMemo, FormEvent, useState, } from 'react';
import useConfig from '@@/use-config';
import parseApi, { Api, Security } from '@/lib/parse-api';
import queryString from 'query-string';
import useRequest from '@/lib/use-request';

interface ApiContextType {
    info: Api;
    fetching: boolean;
    result?: any;
    clearResult: () => void;
}

const ApiContext = createContext<ApiContextType | undefined>(undefined);

interface Props {
    group: string;
    value: string;
}

let securities: { [k: string]: string } = {};

if (window.sessionStorage) {
    try {
        const raw = window.sessionStorage.getItem('api-plugin-securities');
        if (raw) {
            securities = JSON.parse(raw);
        }
    } catch {

    }
}

export function ApiProvider({ value, group, children }: PropsWithChildren<Props>) {

    const config = useConfig().getValue(group) || {};
    const [fetching, setFetching] = useState(false);
    const [result, setResult] = useState<object>();

    const api = useMemo(() => {
        const api = parseApi(value);

        api.group = group;
        api.host = config.url || '';
        api.params = (config.params || []).concat(api.params || []);
        api.explore = config.explore !== undefined ? config.explore : true;

        api.contentType = config.contentType || 'application/x-www-form-urlencoded';

        api.headers = config.headers || {};

        if (config.security && api.security !== false) {
            api.security = config.security as Security;
        }

        return api;
    }, [value, group, config]);

    const request = useRequest();

    const onSubmit = async (e: FormEvent<HTMLFormElement>) => {
        e.preventDefault();

        if (!fetching) {
            try {
                setFetching(true);

                const formData = new FormData(e.currentTarget);

                let params: { [k: string]: string } = {};

                const getDefaultParamValue = (name: string) => {
                    let param = api.params.find(param => param.name === name);
                    if (param) {
                        return param.default;
                    }
                };

                for (const [index, value] of formData.entries()) {
                    if (value && typeof value === 'string') {
                        params[index] = value;
                    } else {
                        let defaultValue = getDefaultParamValue(index);
                        if (defaultValue) {
                            params[index] = defaultValue;
                        }
                    }
                }

                let url = api.host + api.url.replace(/([\/=]):([^/]+)/ig, function(all, prefix, param) {
                    if (params[param] !== undefined) {
                        all = prefix + params[param];
                        delete params[param];
                    }
                    return all;
                });

                const method = api.method;

                const headers = {
                    ...api.headers
                };

                if (api.security) {
                    let security = api.security;
                    let value = securities[api.group] || security.default;
                    switch (security.type) {
                        case 'apiKey':
                            if (security.in === 'header') {
                                headers[security.name] = value;
                            } else {
                                url = queryString.stringifyUrl({
                                    url,
                                    query: {
                                        [security.name]: value
                                    }
                                });
                            }
                            break;
                        case 'oauth2':
                            headers['Authorization'] = `Bearer ${value}`;
                            break;
                    }
                }

                if (method === 'get') {
                    url = queryString.stringifyUrl({
                        url,
                        query: params
                    });
                    params = {};
                }

                const result = await request({
                    method,
                    url,
                    headers,
                    params,
                    contentType: api.contentType
                });


                let body;
                try {
                    //解析json
                    if (result.responseHeaders['content-type'] && result.responseHeaders['content-type'].match(/^application\/json/)) {
                        let matches = result.body.match(/^\w+?\((.*)\);$/);
                        if (matches) {
                            //JSONP
                            body = JSON.parse(matches[1]);
                        } else {
                            body = JSON.parse(result.body);
                        }
                    }
                } catch (e) {

                }

                if (body) {
                    result.body = JSON.stringify(body, null, 4);
                }

                result.params = JSON.stringify(result.params, null, 4);
                result.requestHeaders = JSON.stringify(result.requestHeaders, null, 4);
                result.responseHeaders = JSON.stringify(result.responseHeaders, null, 4);

                setResult(result);
            } finally {
                setFetching(false);
            }
        }
    };

    const clearResult = () => {
        setResult(undefined);
    };

    return <ApiContext.Provider value={{ info: api, fetching, result, clearResult }}>
        <form onSubmit={onSubmit}>
            {children}
        </form>
    </ApiContext.Provider>;
}

export default function useApi() {
    const context = useContext(ApiContext);

    if (!context) {
        throw new Error;
    }

    return context;
}
