import { Param } from '@/lib/parse-api';
import { styled } from 'topwrite';

interface Props {
    param: Param;
}

export default function Field({ param }: Props) {

    switch (param.type) {
        case 'boolean':
            return <Select name={param.name} required={param.required && !param.default}>
                <option></option>
                <option value='true'>true</option>
                <option value='false'>false</option>
            </Select>;
        default:
            return <Input
                name={param.name}
                required={param.required && !param.default}
                placeholder={param.default}
                type='text'
            />;
    }
}

const Select = styled.select`
  display: block;
  height: auto;
  width: 100%;
  background: #fff;
  border: 1px solid rgba(34, 36, 38, .15);
  border-radius: 5px;
  padding: 8px 14px;
  font-size: 14px;
  color: rgba(0, 0, 0, .87);
`;

const Input = styled.input`
  display: block;
  width: 100%;
  margin: 0;
  outline: none;
  line-height: 1.2;
  padding: 8px 14px;
  background: #fff;
  border: 1px solid rgba(34, 36, 38, .15);
  color: rgba(0, 0, 0, .87);
  border-radius: 5px;
  font-size: 14px;
`;
