import { GetComponentProps, styled } from 'topwrite';
import Request from './request';
import Params from '@/components/api/params';
import { ApiProvider } from '@/lib/use-api';
import Results from '@/components/api/results';

export default function Api({ props: { value, parameter } }: GetComponentProps<'x-block'>) {

    return <Container>
        <ApiProvider group={parameter || 'default'} value={value}>
            <Request />
            <Params />
            <Results />
        </ApiProvider>
    </Container>;
}


const Container = styled.div`
  &&& {
    line-height: 1.5em;
    margin-bottom: 20px;

    * {
      font-size: 14px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
`;
